import React from "react"
import { Container, Row, Nav } from "react-bootstrap/"
import { FaLinkedinIn } from 'react-icons/fa';
import { AiOutlineGithub } from 'react-icons/ai';

export default function Footer() {
    return (
        <Container fluid>
            <Row style={{ height: "200px" , display:"flex", fontSize:"18PX" }}>

                    <Nav className="mr-auto between">
                        <Nav.Link
                            href="https://www.linkedin.com/in/francisconovoaparada/">
                            <FaLinkedinIn size="25px" />
                        </Nav.Link>
                        <Nav.Link
                            href="https://github.com/Francisco-Novoa">
                            <AiOutlineGithub size="25px" />
                        </Nav.Link>
                        <span className="text-muted">
                            Pagina realizada usando
                        <a href="https://ghibliapi.herokuapp.com/" target="blank"> <strong> Ghibli Api</strong></a>,
                        <a href="https://react-bootstrap.netlify.app/" target="blank"> React-Bootstrap</a>,
                        <a href="https://react-icons.github.io/react-icons/" target="blank" > React-Icons</a> y
                        <a href="https://es.reactjs.org/docs/getting-started.html" target="blank"> Reactjs</a>.
                        </span>
                    </Nav>
            </Row>
        </Container>
    )
}